* {
  box-sizing: border-box;
  overflow-anchor: none; /* https://developer.mozilla.org/en-US/docs/Web/CSS/overflow-anchor helps with scroll anchoring */
}

/* Defaults from reacct */
.App {
  text-align: center;
  /* font-size:x-small !important; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Represents the play area (strictly bigger than game area), but not sidebars or bottom/top toolbars/hud */
.entire-area {
  width: 100%;
  height: 100%;
  background-color: #000000;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button-zone {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 300;
}
.debug-overlay {
  position: relative;
  top: 1px;
  left: 1px;
  /* width: 100%; */
  width: 0px;
  height: 0px;
  z-index: 300;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

